import * as React from 'react';

class Display extends React.Component {
    constructor(prop){
        super(prop);
    }
    render(){
        let { message } = this.props;
        return (
            <div>
                { message }
            </div>
        );
    }
}
//props
Display.propTypes = {
    message: React.PropTypes.string
};
//props
Display.defaultProps = {
    message: ''
};

export default Display;


